#App {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}

.header {
  background-image: linear-gradient(var(--bgColor), var(--bgColorTransparency), transparent);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
}

.header .headerTitle {
  text-align: center;
  font-family: billiondreams;
  font-size: 4rem;
  padding: 0;
  margin: 0;
  font-weight: 400;
}

.containers {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  margin: 0;
  padding: 0;
  margin-bottom: 1rem;
}

.container.imageContainer {
  margin-top: -1.5rem;
  position: relative;
  z-index: -1;
}

.container.textContainer {
  width: 50vw;
  margin: 1rem;
  padding: 1rem;
  background-color: var(--bgTertiaryColor);
  border-radius: 2rem;
  box-shadow:
  0px 2.6px 2.7px rgba(0, 0, 0, 0.016),
  0px 6.5px 6.9px rgba(0, 0, 0, 0.022),
  0px 13.3px 14.2px rgba(0, 0, 0, 0.028),
  0px 27.4px 29.2px rgba(0, 0, 0, 0.034),
  0px 75px 80px rgba(0, 0, 0, 0.05);
}

.container.imageContainer img {
  width: 100vw;
  height: 550px;
  object-fit: cover;
  box-shadow:
  0px 2.6px 2.7px rgba(0, 0, 0, 0.016),
  0px 6.5px 6.9px rgba(0, 0, 0, 0.022),
  0px 13.3px 14.2px rgba(0, 0, 0, 0.028),
  0px 27.4px 29.2px rgba(0, 0, 0, 0.034),
  0px 75px 80px rgba(0, 0, 0, 0.05);
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  pointer-events: none;
}

.pilot {
  position: absolute;
  padding: 1rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  background-color: var(--bgColorTransparency);
  border-radius: 20px;
  box-shadow:
  0px 2.6px 2.7px rgba(0, 0, 0, 0.016),
  0px 6.5px 6.9px rgba(0, 0, 0, 0.022),
  0px 13.3px 14.2px rgba(0, 0, 0, 0.028),
  0px 27.4px 29.2px rgba(0, 0, 0, 0.034),
  0px 75px 80px rgba(0, 0, 0, 0.05);
}

.pilot:before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  content: "";
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  z-index: -1;
  border-radius: 20px;
}

.menu {
  position: relative;
  display: flex;
  flex-direction: row;
  font-size: 2rem;
  padding-top: 1rem;
  justify-content: space-evenly;
  z-index: 2;
}

.menu span {
  padding: 1rem;
  width: 50px;
  cursor: pointer;
}

.menu span:hover {
  background-color: var(--bgTertiaryColor);
}

.store {
  width: 160px;
}

.store.playStore {
  width: 175px;
}

.store.AppStore {
  padding: 0.5rem;
}

.downloads {
  cursor: pointer;
}

@media only screen and (max-width: 930px) {
  .container.textContainer {
    width: 75vw;
  }
}

@media only screen and (max-width: 630px) {
  .container.textContainer {
    width: 80vw;
  }
}